.ImageBlockInCreate {
    $font-size: 20px;
    $widthImgBlock: 121px;
    .subblock {
        width: 510px;
    }
    .ImgBlock {
        padding: 2px;
        background-color: rgba(0, 0, 0, .1);
        border-radius: 4px;
        cursor: pointer;
        position: relative;
        display: inline-block;
        margin-right: 5px;
    }
    .text {
        width: $widthImgBlock;
        overflow: hidden;
        text-align: center;
        height: $font-size;
        font-size: $font-size;
    }
    img {
        width: $widthImgBlock;
        height: $widthImgBlock;
    }
}

.OtherBlockInCreate {
    $font-size: 20px;
    $border: lightgrey 1px solid;
    width: 513px;
    border-bottom: $border;
    a {
        color: blue;
        display: block;
        font-size: $font-size;
        height: $font-size;
        overflow: hidden;
        width: 509px;
        padding: 2px 2px 6px 2px;
        text-decoration: none;
        outline: none;
        border-top: $border;
    }
}
.SurveyCreateBlock {
    $paddingTop: 6px;
    $paddingLeft: 6px;
    border-radius: 6px;
    background-color: rgba(255, 255, 255, .1);
    margin-bottom: 5px;
    padding: $paddingTop $paddingLeft;
    width: calc(100% - 11px);
    display: inline-block;
    font-size: 18px;
    .lastLine {
        width: calc(100% - 20px);
        .text {
            text-align: center;
            $lineFontSize: 18px;
            $lineHeight: 56px;
            position: relative;
            top: ($lineHeight - $lineFontSize) / 2;
        }
        color: grey;
        border: grey 2px dashed;
    }
    .switchBlock {
        padding: 4px 0;
    }
}
//endregion

//region postFeed

.PostFeed {
    width: calc(100% - 1px);
}
.Post {
    width: 512px;
    padding: 8px;
    margin-bottom: 5px;
    .top {
        font-size: 14px;
        color: gray;
    }
    .text {
        width: 100%;
        word-wrap: break-word;
        text-overflow: fade;
    }
    .surveyBlock {
        margin: 5px 0;
        border-radius: 6px;
        padding: 5px;
    }
    .surveyLine {
        display: flex;
        padding: 4px;
        border-radius: 4px;
        background-color: rgba(255, 255, 255, .2);
        margin-bottom: 5px;
        cursor: pointer;
        .textBlock {
            width: 100%;
            flex-flow: column;
            &.multi {
                width: calc(100% - 40px);
            }
        }
        .number {
            text-align: center;
            display: block;
            width: 100%;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    .PostBottom {
        padding-top: 10px;
        .likesAndDislikesBlock {
            cursor: pointer;
            display: inline-flex;
            background-color: rgba(0, 0, 0, .1);
            height: 36px;
            border-radius: 18px;
            margin-right: 10px;
            .estimationBlock {
                display: flex;
                justify-content: center;
                padding: 6px;
                font-size: 20px;
                height: 24px;
                svg {
                    margin-right: 12px;
                }
                &.likes {
                    border-radius: 18px 0 0 18px;
                    &:after {
                        content: "";
                        position: relative;
                        left: 6px;
                        background-color: rgba(255, 255, 255, .2);
                        width: 1px;
                        height: 24px;
                    }
                }
                &:hover {
                    background-color: rgba(255, 255, 255, 0.3);
                }
                &.dislikes {
                    border-radius: 0 18px 18px 0;
                }
            }
        }
        .iconPanel {
            width: 512px;
            justify-content: space-between;
            align-items: center;
            display: flex;
        }
        .commentIcon {
            display: flex;
            background-color: rgba(0, 0, 0, .1);
            border-radius: 50%;
            width: 36px;
            height: 36px;
            cursor: pointer;
            align-items: center;
            justify-content: center;
            &:hover {
                background-color: rgba(255, 255, 255, 0.3);
            }
            .icon {
                fill: #fff;
            }
        }
        .trash {
            padding: 5px;
            border-radius: 50%;
            cursor: pointer;
            fill: white;
            &:hover {
                background-color: rgba(255, 255, 255, .2);
            }
        }
        .CommentBlock {
            width: 510px;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 5px;
            flex-flow: column;
            border-top: 1px white solid;
            .noCommentsText {
                text-align: center;
                color: #f5f5f5;
            }
            .inputLine {
                display: flex;
                position: relative;
                top: 5px;
                width: 550px - 40px;
                .input {
                    width: 490px;
                }
            }
            .commentLine {
                border-bottom: 1px solid lightgrey;
                width: 100%;
                padding: 5px;
                display: flex;
                flex-flow: column;
                .main {
                    display: flex;

                    .like {
                        width: 25px;
                        padding: 5px;
                        height: 25px;
                        font-size: 24px;
                        display: flex;
                        border-radius: 50%;
                        align-items: center;
                        justify-content: center;
                        &:hover {
                            background-color: rgba(255, 255, 255, .4);
                        }
                    }
                    .text {
                        display: inline-block;
                        overflow: hidden;
                        width: 420px;
                    }
                }
            }
        }
    }
}

.miniAvatar {
    color: lightgrey;
    display: inline-block;
    position: relative;
    width: 30px;
    height: 30px;
    left: 15px;
    top: 20px;
}
.miniAvatar div {
    transform-origin: 5px 5px;
    animation: miniAvatar 1.2s linear infinite;
}
.miniAvatar div:after {
    content: " ";
    display: block;
    position: absolute;
    top: 3px;
    left: 28px;
    width: 2px;
    height: 6px;
    border-radius: 20%;
    background: #fff;
}
.miniAvatar div:nth-child(1) {
    transform: rotate(0deg);
    animation-delay: -1.1s;
}
.miniAvatar div:nth-child(2) {
    transform: rotate(30deg);
    animation-delay: -1s;
}
.miniAvatar div:nth-child(3) {
    transform: rotate(60deg);
    animation-delay: -0.9s;
}
.miniAvatar div:nth-child(4) {
    transform: rotate(90deg);
    animation-delay: -0.8s;
}
.miniAvatar div:nth-child(5) {
    transform: rotate(120deg);
    animation-delay: -0.7s;
}
.miniAvatar div:nth-child(6) {
    transform: rotate(150deg);
    animation-delay: -0.6s;
}
.miniAvatar div:nth-child(7) {
    transform: rotate(180deg);
    animation-delay: -0.5s;
}
.miniAvatar div:nth-child(8) {
    transform: rotate(210deg);
    animation-delay: -0.4s;
}
.miniAvatar div:nth-child(9) {
    transform: rotate(240deg);
    animation-delay: -0.3s;
}
.miniAvatar div:nth-child(10) {
    transform: rotate(270deg);
    animation-delay: -0.2s;
}
.miniAvatar div:nth-child(11) {
    transform: rotate(300deg);
    animation-delay: -0.1s;
}
.miniAvatar div:nth-child(12) {
    transform: rotate(330deg);
    animation-delay: 0s;
}
@keyframes miniAvatar {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
